@import url(https://fonts.googleapis.com/css2?family=Merriweather:wght@900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header--title {
  color: #2f3e6a;
  font-family: "Merriweather", serif;
  font-size: 26px;
  box-sizing: border-box;
  display: inline-block;
}

.checkbox--container {
  list-style: none;
  padding: 0;
}

.checkbox--container li {
  display: inline-block;
  width: 250px;
  height: 50px;
  font-size: 18px;
}

.checkbox--align-items {
  display: inline-flex;
  align-items: center;
  cursor: pointer;
}

.checkbox--hide-old-checkbox {
  display: none;
}

.checkbox--new-radio {
  width: 19px;
  height: 19px;
  border-radius: 50%;
  border: 0.13rem solid #2f3e6a;
  margin-right: 8px;
}

.checkbox--new-radio:after {
  content: "";
  margin-left: 2.3px;
  margin-top: 2.6px;
  width: 75%;
  height: 75%;
  display: block;
  background-color: #2f3e6a;
  border-radius: 50%;
  transform: scale(0);
}

.checkbox--hide-old-checkbox:checked + .checkbox--new-radio:after {
  transform: scale(1);
}

.textbox-container {
  font-size: 18px;
  font-family: "Mulish", sans-serif;
}

.textbox--color {
  color: #2f3e6a;
}

.textbox--input {
  font-family: inherit;
  font-size: 16px;
  margin-top: 15px;
  width: 85%;
  height: 80px;
  border-radius: 5px;
  background-color: #fbfcff;
  border: 0.5px solid #e3e8f5;
}

.textbox--note {
  font-size: 14px;
  margin-right: 70px;
}

.button--container {
  display: inline-block;
  margin-top: 10px;
}

.button--general {
  width: 90px;
  height: 50px;
  border-radius: 5px;
  font-family: "Mulish", sans-serif;
  font-size: 18px;
  border: none;
}

.button--cancel {
  background-color: #ebeffa;
  color: #1b2b5c;
}

.button--submit {
  background-color: #1b2b5c;
  color: #ffffff;
  margin-left: 20px;
}

.page--container {
  width: 600px;
  height: 550px;
  background-color: #ffffff;
  margin: 0px auto;
  box-sizing: border-box;
  margin-top: 100px;
  border-radius: 20px;
  box-shadow: 5px 5px 10px #e1e7f6;
}

.page--inner-container {
  width: 85%;
  height: 80%;
  margin-left: 50px;
  display: inline-block;
  margin-top: 50px;
  font-family: inherit;
}

